"use client"
import React, { useState } from 'react'
import ChecklistItem from './ChecklistItem';
import { saladList } from '@/assets/assets';

const Contact = () => {

  const [result, setResult] = useState("");
  const [activeItemsCount,setActiveItemsCount] = useState (0);

  const items = saladList.map((item, index) => {
    return (
      <ChecklistItem
        key={index}
        index={index}
        title = {item.title}
        weight = {item.weight}
        category = {item.category}
        activeItemsCount = {activeItemsCount}
        setActiveItemsCount = {setActiveItemsCount}
      />
    );
  });

  const onSubmit = async (event) => {
    event.preventDefault();
    setResult("שולח...");
    const formData = new FormData(event.target);

    formData.append("access_key", "b7897d0e-ed09-4dc4-aa69-50b167e2ec6d");

    const response = await fetch("https://api.web3forms.com/submit", {
      method: "POST",
      body: formData
    });

    const data = await response.json();

    if (data.success) {
      setResult("ההודעה נשלחה בהצלחה! תודה שבחרת סלטי שמואל, המשלוח יגיע לביתך ביום חמישי לפני הבישולים לשבת.");
      event.target.reset();
    } else {
      console.log("Error", data);
      setResult(data.message);
    }
  };


  return (
    <div id='contactForm' >
     <form onSubmit={onSubmit} className='max-w-2xl mx-auto'>
            <div className='grid grid-cols-auto gap-6 mt-10 mb-8'>
                <input
                    type='text' name='name' placeholder='שם ומשפחה' required
                    className='flex-1 p-3 outline-none border-[0.5px] border-gray-400 rounded-md bg-white
                                dark:bg-darkHower/30 dark:border-white/90 ' />
                <input 
                    type='text' name='address' placeholder='כתובת מלאה - עיר רחוב ומספר בית' required
                    className='flex-1 p-3 outline-none border-[0.5px] border-gray-400 rounded-md bg-white
                                dark:bg-darkHower/30 dark:border-white/90 ' />

               <input
                    type='text' name='phone' placeholder='מספר טלפון' required
                    className='flex-1 p-3 outline-none border-[0.5px] border-gray-400 rounded-md bg-white
                                dark:bg-darkHower/30 dark:border-white/90 ' />

                <input 
                    type='email' name='email' placeholder='אימייל' required 
                    className='flex-1 p-3 outline-none border-[0.5px] border-gray-400 rounded-md bg-white
                                dark:bg-darkHower/30 dark:border-white/90 '/>
            </div>
            <h4 
                className='mb-2 text-xl font-Outfit text-yellow-500/90 font-semibold'>אנא סמן שמונה מוצרים מתוך הרשימה !</h4>
            <fieldset
                transition={{delay:1.5,duration:0.6}} 
                className='p-6 bg-white border border-gray-400/80 rounded-md  shadow-sm
                            grid grid-cols-1 sm:grid-cols-2 gap-4'
             >

              {items}
             </fieldset>
             
             <h4 
                className='mt-6 mb-2 text-lg font-Outfit text-yellow-500/80 font-semibold'>דרך התשלום המועדפת עליך:</h4>
             <fieldset
                className='max-w-fit p-2 flex gap-6'
             >
                <div className="flex items-center mb-4">
                  <input id="country-option-1" type="radio" name="payment" value="תשלום במזומן" className="w-4 h-4 border-gray-300 focus:ring-2 focus:ring-blue-300 dark:focus:ring-blue-600 dark:focus:bg-blue-600 dark:bg-gray-700 dark:border-gray-600" />
                  <label htmlFor="country-option-1" className="block ms-2  text-md font-medium text-gray-800/90 dark:text-gray-300">
                    תשלום במזומן
                  </label>
                </div>

                <div className="flex items-center mb-4">
                  <input id="country-option-2" type="radio" name="payment" value="העברה בביט" className="w-4 h-4 border-gray-300 focus:ring-2 focus:ring-blue-300 dark:focus:ring-blue-600 dark:focus:bg-blue-600 dark:bg-gray-700 dark:border-gray-600" />
                  <label htmlFor="country-option-2" className="block ms-2 text-md font-medium text-gray-800/90 dark:text-gray-300">
                    העברה בביט
                  </label>
                </div>
            </fieldset>

            <textarea 
                name='message' rows='6' placeholder='הודעה...' required 
                className='mt-6 w-full p-4 outline-none border-[0.5px] border-gray-400 rounded-md bg-white mb-6
                dark:bg-darkHower/30 dark:border-white/90'></textarea>
            

             <button
               type='submit' className='text-lg py-3 px-8 w-max flex items-center justify-between gap-2 bg-black/80
             text-white rounded-full mx-auto hover:bg-black duration-500 font-sans dark:bg-transparent dark:border-[0.5px] dark:hover:bg-darkHower'>שלח הודעה
                <Image src="/vercel.svg"  alt='send' width={20} height={20}/>
             </button>
            
             <p className='mt-4'>{result}</p>
        </form>
               
    </div>
  )

}

export default Contact
